<template>
<main-layout :backgroundImage="`${$cloudinary}main.png`" backgroundOffset="50">

  <template #header>
    <personal-info-header :userinfo="`${myPosition} / ${rating.length} місце у рейтингу`"/>
  </template>

  <template #body>
    <div class="section-name">Рейтинг Учасників</div>
    <br/>
    <div class="rating-section">
      <div class="rating-item" :class="{'rating-item-me': i == myPosition}" v-for="i in rating.length" :key="i">
        <span class="user-index">{{ i }}.</span>
        <span class="user-icon material-icons-outlined" :style="{color: userScoreColors[i-1]}">account_circle</span>
        <span class="user-name">{{ rating[i-1].username }}</span>
        <!-- <span class="score-icon material-icons-outlined" :style="{color: userScoreColors[i-1]}">workspace_premium</span> -->
        <b>{{ rating[i-1].score }} балів</b>
      </div>
    </div>
  </template>

</main-layout>
</template>

<script>
import mainLayout from '@/layouts/main-layout.vue'
import personalInfoHeader from '@/components/personal-info-header.vue'
export default {
  components: { mainLayout, personalInfoHeader },
    data() { return {
    username: localStorage.getItem('username')
  }},
  computed: {
    rating() { return this.$store.state.rating.filter(e => e !== 'ysteeltyes') },
    userScores() { return this.rating.map(e => e.score) },
    userScoreColors() { return this.userScores.map(e => this.mix3(e / this.userScores[0] * 100)) },
    myPosition() { return this.rating.map(e => e.username).indexOf(this.username) + 1 }
  }
}
</script>

<style lang="scss" scoped>
//* {border: 1px solid red}
.section-name {
  font-size: 1.15em;
  font-weight: bold;
  padding: 1em 1.5em;
  margin-bottom: .5em;
  border-radius: 4px;
  text-transform: uppercase;
  background: rgba(0,0,0,.025);
}
.rating-section {
  margin: auto;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 2px 16px 5px rgba(0,0,0,.05);
}
.rating-item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: .7em;
  padding: 2.5vmin 3vmax;
  border-bottom: 1px solid #0001;
  .user-index { 
    width: 2ch;
    text-align: center;
    font-size: 1.33em;
    font-weight: bold;
    font-family: 'Oswald';
    color: #333;
  }
  .user-icon { font-size: 2em }
  .user-name { flex-grow: 1 }
  .user-score { font-weight: 700 }
}
.rating-item-me {
  .user-name { 
    font-weight: bold;
    color: #FFB516; 
  }
}
</style>